body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.terminal_xtermViewport__3AESn {
    overflow-y: scroll !important;
}

.terminal_xtermViewport__3AESn::-webkit-scrollbar {
    display: none; /* This will hide the scrollbar in WebKit browsers like Chrome and Safari */
}

.terminal_xtermViewport__3AESn {
    scrollbar-width: none; /* This will hide the scrollbar in Firefox */
}
:root {
  --light-color: #fffff0;
  --dark-color: #0c1117;
}

body, html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow: hidden;
}

.App-content {
  flex: 1 1;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  margin: 0;
}

.App-content {
  background-color: var(--background-color);
}

.loader, .fade-in {
  transition: opacity 1s ease-in-out;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: var(--background-color);
}

.loader.visible, .fade-in.visible {
  opacity: 1;
}
